
.statistics{
position: relative;
display: block;
margin: 0;
border: 2px solid black;
border-radius: 4px;
padding-top: 16px;
flex-direction: column;
}

.title{
font-size: medium;
    margin-top: 20px;
    text-align: center;
    color: dimgray;
}

.statlist{
    list-style: none;
    display: inline-flex;

    padding-inline-start: 0;
    margin: 0;
    border: 1px solid black;
    background-color: aquamarine;
}
.item{
    margin-left: 0;
    text-align: center;
    border: 1px solid #bbb;
    padding: 0 2px 0 4px;
}
.label,.percentage{
    font-size: medium;
    text-align: center;
    color: dimgray;
    margin-left: 5px;
    
}